.notification_card {
  &__enter {
    opacity: 0;
    transform: translateX(100%);
  }

  &__enterActive {
    opacity: 1;
    transform: none;
    transition: opacity 500ms, transform 500ms;
  }

  &__exit {
    opacity: 1;
  }

  &__exitActive {
    opacity: 0;
    transform: translateX(100%);
    transition: opacity 500ms, transform 500ms;
  }
}

.notification {
  &_card {  
    @extend %content;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  } 

  &_type {
    flex-basis: 50%;
    text-transform: uppercase;
  }

  &_btn {
    background: none;
    outline: none;
    border: none;
    color: var(--on-element-color);
    transition: color 0.1s linear;

    &:hover { 
      color: white
    }
  }
}

%content {
  background-color: var(--element-color);
  border-radius: 8px;
  border: 1px solid currentColor;
  padding: 8px;
}