@use 'setting' as f;
@import url("https://fonts.googleapis.com/css?family=Montserrat:100,100i,200,200i,300,300i,400,400i,500,500i,600,600i,700,700i,800,800i,900,900i&display=swap&subset=cyrillic,cyrillic-ext,latin-ext");

:root[data-theme=dark] {

}

:root {
  // --primary-color: #{f.$primary-color-dark};
  // --on-primary-color: #{f.$on-primary-color-dark};

  --background-color: #{f.$background-color-dark};
  --on-background-color:#{f.$on-background-color-dark};

  --element-color: #{f.$element-color-dark};
  --on-element-color: #{f.$on-element-color-dark};

  --error-color: #{f.$error-color-dark};
  --on-error-color: #{f.$on-error-color-dark};

  --primary-variant-color: #{f.$primary-variant-color};

  --second-on-color: #{f.$second-on-color};

  --input-back-color: #{f.$input-back-color};

  --hovered: #{f.$hovered};
  --focused: #{f.$focused};
  --pressed: #{f.$pressed};

  --nav-height: 5rem;
  --default-padding: 12px;

  --default-box-shadow: 0px 0px 24px rgb(36, 36, 36);

  --color-animation: color 0.25s ease-in-out;
  --background-color-animation: background-color 0.25s ease-in-out;
  
  --container-max-widths-sm: #{f.$container-max-widths-sm};
  --container-max-widths-md: #{f.$container-max-widths-md};
  --container-max-widths-lg: #{f.$container-max-widths-lg};
  --container-max-widths-xl: #{f.$container-max-widths-xl};
  --container-max-widths-xll: #{f.$container-max-widths-xll};
}

:root::-webkit-scrollbar {
  overflow: auto;
  width: 0.5rem;
  height: 0.5rem;
  background-color: transparent;
  
}
:root::-webkit-scrollbar-thumb {
  background-color: var(--on-element-color);
  border-radius: 5px;
}

:root {
  scrollbar-color: var(--on-element-color) transparent;
  scrollbar-width: thin;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
  line-height: 1.5;

  scroll-behavior: smooth; 
}

body {
  color: var(--on-background-color);
  font-family: f.$font-family;
  line-height: inherit;

  background-color: var(--background-color);

  overscroll-behavior: none;
  scrollbar-gutter: stable;

  height: 100%;
  width: 100%;
}

main {
  min-height: 100%;
  margin-top: var(--nav-height);
  box-sizing: border-box;
  padding: 0 var(--default-padding);
}

#root {
  max-width: f.$container-max-widths-xll;
  margin: 0 auto;
}

h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

