@use '/src/style/setting' as s;

.footer {
  display: flex;
  flex-direction: column;
  gap: 28px;

  box-sizing: border-box;
  padding: 20px var(--default-padding);
  margin-top: 40px;

  &_container {
    display: flex;
    justify-content: space-between;
  }

  &_contact_address {

  }

  &_logo {
    width: 100px;
    height: 100px;
  }

  &_col {
    @extend .flex-settings;
    flex-direction: column;
  }

  &_link {
    font-size: 12px;
    opacity: 1;
    text-decoration: none;
    color: var(--on-element-color);
    transition: 0.2s ease-in-out opacity;
  }

  &_contact_container {
    @extend .flex-settings;
  }
}

.flex-settings {
  display: flex;
  gap: 12px
}

@media (hover: hover) and (pointer: fine) {
  .footer {
    &_link:hover {
      opacity: 0.75;
    }
  }
}

@media (max-width: s.$container-max-widths-lg) {
  .footer{
    &_container {
      justify-content: center;
      flex-wrap: wrap;
      gap: 28px
    }
  }
}