@use '/src/style/setting' as s;
@use '/src/style/z-index-constant' as z;

.navbar {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: z.$ui-index-4;

  background-color: var(--background-color);
  border-bottom: 1px currentColor solid;

  min-height: var(--nav-height);
  width: 100%;

  &_container {
    display: flex;
    align-items: center;
    max-width: s.$container-max-widths-xll;
    width: 100%;
    padding: 0 var(--default-padding);
    height: var(--nav-height);
  }

  &_open {
    @extend .navbar_container;
  }

  &_logo {
    height: 50px;
  }

  &_list {
    @extend %nav-item-settings;
    list-style: none;

    &_item {
      margin-left: 20px;
    }
  }

  &_btn {
    display: none;
  
    background-color: transparent;
    border: none;
    color: var(--on-element-color);
    cursor: pointer;
  
    margin-left: auto;
    width: 48px;
    height: 48px;
  }

  &_link {
    color: var(--on-element-color);
    text-decoration: none;

    transition: var(--color-animation);
  }

  &_left {
    @extend %nav-item-settings;
  }

  &_center {
    @extend %nav-item-settings;
    margin: 0 auto
  }

  &_right {
    @extend %nav-item-settings;
  }
}

.basket_count {
  position: relative;

  &::after {
    display: flex;
    justify-content: center;
    align-items: center;
    top: -1rem;
    right: -1rem;
    border-radius: 50%;
    background-color: var(--on-element-color);
    color: black;
    font-size: 0.8rem;
    font-weight: 500;

    content: attr(data-count) "";
    position: absolute;

    width: 1.3rem;
    height: 1.3rem;
  }
}

%nav-item-settings {
  display:flex;
  align-items: center;

  height: 100%;
}

@media (hover: hover) and (pointer: fine) {
  .navbar {
    &_link[aria-current='page'],
    &_link:hover {
      color: var(--second-on-color);
    }
  }
}

@media (max-width: s.$container-max-widths-lg) {
  .navbar {

    &_open {
      display: flex;
      flex-direction: column;
      gap: 20px;
      position: absolute;
      top: var(--nav-height);
      right: 0;
      z-index: z.$ui-index-3;

      background-color: var(--background-color);
      border-bottom: 1px currentColor solid;

      padding: 20px 0;
      height: auto;
    }

    &_list {
      flex-direction: column;
      gap: 20px;

      &_item {
        margin: 0;
      }
    }

    &_center,
    &_right {
      height: auto;
    }

    &_btn {
      display: block;
    }
  }

  .navbar_open.navbar_hidden {
    display: none;
  }
}
