.arrow {
  display: flex;
  flex-direction: column;
  width: max-content;
  position: absolute;
  cursor: pointer;
  bottom: 40px;
  left: 50%;
  transform: translateX(-50%);

  &_first {
    animation: arrow-animation 1s 0s infinite alternate linear;
  }

  &_second {
    animation: arrow-animation 1s 0.5s infinite alternate linear;
  }

  &_third {
    animation: arrow-animation 1s 1s infinite alternate linear;
  }
}

@keyframes arrow-animation {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}