.notification {
  display: flex;
  flex-direction: column;
  justify-self: center;
  align-items: center;
  gap: 20px;
  position: fixed;
  bottom: 0;
  right: 0;

  padding: 10px;
  width: 240px;
  height: auto;

  &_card {  
    @extend %content;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 100%;
  } 

  &_hide {
    @extend %content;
    width: 100%;
  }
}

@keyframes open {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(-100%);
    opacity: 1;
  }
}

%content {
  background-color: var(--element-color);
  border-radius: 8px;
  border: 1px solid currentColor;
  padding: 8px;
}