@use '/src/style/setting' as s;

.home {
  display: flex;
  flex-direction: column;
  gap: 28px;

  width: 100%;

  &_main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    background-image: 
      linear-gradient(to right, var(--background-color), 8%,transparent 16%), 
      linear-gradient(to left, var(--background-color), 8%,transparent 16%), 
      linear-gradient(to top, var(--background-color), 8%,transparent 16%),   
      url('/src/assets/back.url.jpg');
    background-repeat: 
      no-repeat;
    background-size: 
      100% calc(100vh - var(--nav-height)),
      100% calc(100vh - var(--nav-height)),
      100% calc(100vh - var(--nav-height)),
      s.$container-max-widths-xll s.$container-max-widths-lg;
    background-position: 50% 0;

    min-height: 500px;
    height: calc(100vh - var(--nav-height));
  }

  &_name {
    font-size: 4rem;
  }

  &_name,
  &_text {
    text-transform: uppercase;
    text-align: center;
  }

  &_container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 40px;
    
    transform: translateY(-80px);
  }
}

@media (max-width: s.$container-max-widths-md) {
  .home {
    &_name {
      font-size: 3rem;
    }
  }
}