.mentors {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  gap: 40px;

  min-height: 50vh;
  width: 100%;

  &_info {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  &_name {
    font-size: 2rem;
    font-weight: 700;
  }

  &_photo {
    border-radius: 50%;
    
    min-width: 200px;
    min-height: 200px;
  }
}